.calculation {
    // .calculation__inner

    &__inner {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        position: relative;
    }
    // .calculation__header-block

    &__header-block {
        text-align: start;
        &::after {
            bottom: 23px;
        }
    }

    // .calculation__title

    &__title {
        max-width: 626px;
    }

    // .calculation__content

    &__content {
        display: block;
        position: absolute;
        left: 0;
    }

    // .calculation__form

    &__form {
        max-width: 626px;
        padding: 50px 50px 60px 50px;
        background-color: #363a4d;
        border-radius: 20px;
        box-shadow: 5px 15px 40px rgba(0, 0, 0, 0.32);
    }

    // .calculation__form-row

    &__form-row {
        display: flex;
        flex-wrap: wrap;
        margin: 0 -15px;
    }

    // .calculation__form-col

    &__form-col {
        padding: 0px;
        flex: 0 0 auto;
        width: 100%;
        padding: 0 15px;
    }

    // .calculation__form-elements

    &__form-elements {
        margin-bottom: 20px;
    }

    // .calculation__form-col-big

    &__form-col-big {
        flex: 0 0 auto;
        width: 100%;
        margin-bottom: 26px;
        padding: 0 15px;
    }

    &__label-checkbox {
        padding: 0 0 0 40px;
    }
    // .calculation__button

    &__button {
        position: relative;
        border: none;
        padding: 23px 97px 23px 33px;
        @media (max-width: 430px) {
            padding: 23px 53px 23px 23px;
            font-size: 16px;
        }
    }

    // .calculation__images

    &__images {
        display: block;
        z-index: 2;
        position: relative;
        min-height: 894.99px;
        max-width: 610px;
        width: 100%;
        @media (max-width: 1150px) {
            min-height: 843.64px;
            max-width: 575px;
        }
        @media (max-width: 1100px) {
            min-height: 733.6px;
            max-width: 500px;
        }
    }
    &__img {
        position: absolute;
        max-width: 100%;
        height: auto;
        top: 60px;
        right: -60px;
    }

    &__form-file-label {
        margin-bottom: 12px;
    }

    &__imput-file {
        width: 0.1px;
        height: 0.1px;
        opacity: 0;
        overflow: hidden;
        position: absolute;
        z-index: -1;
    }
    &__label-file {
        display: flex;
        justify-content: center;
        align-items: center;
        max-width: 526px;
        width: 100%;
        font-weight: 400;
        font-size: 15px;
        line-height: 126%;
        border: 1px dashed rgba(255, 255, 255, 0.26);
        border-radius: 10px;
        margin: 0;
        padding: 14px 0;
        cursor: pointer;
        &:hover {
            border: 1px solid rgb(138, 132, 132);
            box-shadow: rgb(211 211 232 / 82%) 0px 0px 11px 0px;
        }
    }
}
