.cooperation {
    &__container-fluid {
        width: 100%;
        border-radius: 1.25rem;
        border: none;
    }

    // cooperation__bg

    &__bg {
        background: linear-gradient(0deg, #363a4d, #363a4d), #f7f7f7;
        border-radius: 20px;
    }
    // .cooperetion__inner

    &__inner {
        display: flex;
        flex-direction: column;
        padding: 5.8125rem 0;
    }

    // .cooperetion__header

    &__header {
        text-align: center;
        margin-bottom: 7.1875rem;
    }

    // .cooperetion__header-title

    &__title {
        margin-bottom: 1.5rem;
    }

    // .cooperetion__header-subtitle

    &__header-subtitle {
        font-weight: 300;
        font-size: 1.5rem;
        margin: 0;
    }

    // .cooperetion__content

    &__content {
        display: flex;
        flex-direction: column;
    }

    // .cooperetion__contnet-box

    &__contnet-box {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 3rem;
        &:last-child {
            margin-bottom: 0;
        }
    }
}
.block-adaptive {
    display: flex;
    flex-direction: column;
}
.cooperation {
    // .cooperation__info-list

    &__info-list {
        max-width: 33rem;
        padding: 0 10px 0 10px;
        display: flex;
        flex-direction: column;
        margin: 0;
        text-align: justify;
    }

    // .cooperation__info-item

    &__info-item {
        font-weight: 300;
        font-size: 1.0625rem;
        line-height: 156.5%;
        margin-bottom: 1.25rem;
        list-style: disc;
        list-style-position: inside;
        &:last-child {
            margin-bottom: 0px;
        }
    }

    // .cooperation__image
    &__image {
        padding: 0px;
    }
    &__image img {
        border-radius: 1.25rem;
        width: 100%;
        height: auto;
        filter: drop-shadow(5px 15px 40px rgba(11, 24, 97, 0.32));
    }

    // .cooperation__btn

    &__button {
        display: flex;
        align-items: center;
        position: relative;
        margin-top: 3rem;
        padding: 23px 75px 23px 33px;
        max-width: 243px;
    }
    &__btn-icon {
        position: absolute;
        right: 1.3125rem;
    }
}
