//     decktop-h2title  mixin           @include decktop-h2title(700,2.25rem,124.5%,uppercase)
@mixin decktop-h2title($fw, $fs, $lh, $ttr) {
    font-weight: $fw; //700
    font-size: $fs; //36px
    line-height: $lh; //124.5%
    text-transform: $ttr; //uppercase
}
// font-weight: 700; font-size: 2.25rem; line-height: 124.5%; text-transform: uppercase;

//     mobile-h2title  mixin           @include mobile-h2title(700,1.5rem,124.5%,uppercase)
@mixin mobile-h2title($fw, $fs, $lh, $ttr) {
    font-weight: $fw; //700
    font-size: $fs; //24px
    line-height: $lh; //124.5%
    text-transform: $ttr; //uppercase
}
// font-weight: 700; font-size: 1.5rem; line-height: 124.5%; text-transform: uppercase;

//    titel-slider   mixin           @include titel-slider(700,1.375rem,124.5%,uppercase)
@mixin titel-slider($fw, $fs, $lh, $ttr) {
    font-weight: $fw; //700
    font-size: $fs; //22px
    line-height: $lh; //124.5%
    text-transform: $ttr; //uppercase
}
// font-weight: 700; font-size: 1.375rem; line-height: 124.5%; text-transform: uppercase;

//    subtitle-slider   mixin           @include subtitel-slider(400,0.9375rem,124.5%)
@mixin subtitel-slider($fw, $fs, $lh) {
    font-weight: $fw; //400
    font-size: $fs; //15px
    line-height: $lh; //124.5%
}
// font-weight: 400; font-size: 0.9375rem; line-height: 124.5%;

//     btn mixin              @include btn(600,1rem,#ffffff,146%,5.625rem,#E58D35)
@mixin btn($fw, $fs, $col, $lh, $br, $bgcol) {
    font-weight: $fw; //600
    font-size: $fs; //16px
    color: $col; //#fff
    line-height: $lh; //146%
    border-radius: $br; //90px
    background-color: $bgcol; //#E58D35
}
// font-weight: 600; font-size: 1rem; color: #fffff; line-height: 146%; border-radius: 5.625rem, background-color: #E58D35;
