@media screen and (min-width: 991px) {
    .calculation {
        &__header-block {
            &::after {
                display: none;
            }
        }
        &__title {
            &::after {
                content: "";
                position: absolute;
                display: block;
                bottom: 24px;
                left: 85%;
                transform: translate(-50%, -50%);
                width: 85px;
                height: 2px;
                background-color: $after-background-color-lightgreen;
            }
        }
        &__form {
            box-shadow: 0 0 10px rgba(0, 0, 0, 0.7);
        }
        &__form-col {
            flex: 0 0 auto;
            width: 50%;
        }
    }
    .about {
        &__header-block {
            &::after {
                left: 0 + 42.5px;
            }
        }
    }
}
