.services {
    // .services__container-fluid

    &__container-fluid {
        position: relative;
    }

    // .services__bg

    &__bg {
        background: linear-gradient(0deg, #363a4d, #363a4d), #f7f7f7;
        border-radius: 20px;
    }
    // .services__inner

    &__inner {
        padding: 90px 0 100px 0;
    }

    // .services__slider

    &__slider {
        position: relative;
    }

    // .services__slider-item

    &__slider-item {
        background: center/cover no-repeat;
        position: relative;
        max-width: 360px;
        width: 100%;
        padding: 250px 23px 33px 30px;
        border-radius: 20px;
        box-shadow: 5px 15px 40px rgba(30, 35, 66, 0.15);

        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        margin: 0 15px;
        @media (max-width: 450px) {
            max-height: 420px;
        }

        &::before {
            content: "";
            display: block;
            position: absolute;
            width: 100%;
            height: 100%;
            left: 0;
            top: 0;
            border-radius: 20px;
            background: linear-gradient(0deg, rgba(4, 5, 17, 0.91) 0%, rgba(88, 88, 88, 0) 100%);
        }
    }

    // .services__slider-content

    &__slider-content {
        position: relative;
        z-index: 4;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }

    // .services__subtitle

    &__subtitle-slider {
        max-width: 307px;
        margin-bottom: 14px;
    }

    // .services__button

    &__button {
        padding: 14px 24px;
    }
}
// .services-slidernav

.slick-track {
    display: flex;
}
.slick-list {
    overflow: hidden;
}
.slick-dots li {
    position: relative;
    opacity: 0.9;
    font-size: 0;
    margin: 0 13px;
    width: 8px;
    height: 8px;
    border: 2px solid $global-primary-color-text;
    border-radius: 5.625rem;
    list-style: none;
    & button {
        position: absolute;
        width: 8px;
        height: 8px;
        font-size: 0;
        left: -2px;
        top: -2px;
        border-radius: 5.625rem;
        &:hover {
            background-color: $global-primary-color-text;
        }
    }
    &.slick-active {
        background-color: $global-primary-color-text;
    }
}
