.choose {
    // .choose__container-fluid

    &__container-fluid {
        @media (max-width: 991px) {
            padding: 0;
        }
    }

    // .choose__bg

    &__bg {
        background: linear-gradient(0deg, #363a4d, #363a4d), #f7f7f7;
        border-radius: 20px;
        padding: 90px 0 64px 0;
        @media (max-width: 991px) {
            border-radius: 0;
        }
    }

    // .choose__container

    &__container {
    }

    // .choose__inner

    &__inner {
        position: relative;
    }

    // .choose__header-block

    &__header-block {
    }

    // .choose__title

    &__title {
    }

    // .choose__advantages

    &__advantages {
    }
}

.advantages {
    // .advantages__content

    &__content {
    }

    // .advantages__row

    &__row {
        display: flex;
        flex-wrap: wrap;
    }

    // .advantages__columns

    &__columns {
        flex: 0 0 auto;
        min-width: 263px;
        width: 25%;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-bottom: 58px;
        @media (max-width: 1200px) {
            width: 50%;
        }
        @media (max-width: 991px) {
            padding: 20px 0 0 0;
        }
    }

    // .advantages__images

    &__images {
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        width: 93px;
        height: 93px;
        background-color: $after-background-color-lightgreen;
        border-radius: 90px;
        margin-bottom: 32px;
        &::after {
            content: "";
            position: absolute;
            display: block;
            width: 113px;
            height: 113px;
            border-radius: 90px;
            border: 1px solid rgba(108, 113, 136, 1);
        }
    }

    // .advantages__text

    &__text {
        font-weight: 400;
        font-size: 18px;
        line-height: 124.5%;
        text-align: center;
    }
}
