.faq {
  &__content {
    display: flex;
    justify-content: space-between;
  }
  &__form-question {
    background: #363a4d;
    box-shadow: 5px 15px 40px rgba(11, 24, 97, 0.32);
    border-radius: 20px;
    padding: 38px 20px;
    max-width: 390px;
    max-height: 573px;
  }
  &__form-header {
    display: flex;
    flex-direction: column;
    margin-bottom: 23px;
  }
  &__form-title {
    font-weight: 700;
    font-size: 24px;
    line-height: 126%;
    margin-bottom: 8px;
  }
  &__form-subtitle {
    font-weight: 400;
    font-size: 17px;
    line-height: 126%;
  }
  &__form {
    display: flex;
    flex-direction: column;
    gap: 14px;
  }
  &__button {
    position: relative;
    border: none;
    margin-top: 12px;
    padding: 23px 65px 23px 33px;
  }
  &__btn-icon {
    position: absolute;
    right: 1.3125rem;
  }
}

#question {
  height: 70px;
}

.accordion {
  margin-right: 34px;
  max-width: 716px;
}
.tabs {
  background-color: transparent;
  padding: 21px 20px 21px 34px;
  border-radius: 10px;
  border: 1px solid #3a444e;
}

.tabs:not(:last-child) {
  margin-bottom: 16px;
}

.tabs__accordion {
  position: relative;
  max-width: 650px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-weight: 700;
  font-size: 18px;
  line-height: 125.5%;
  color: #ffffff;
  text-align: left;
  background-color: transparent;
  border: none;
  outline: none;
  margin: 0;
  padding-right: 126px;
  cursor: pointer;
}
.tabs__panel {
  display: none;
  overflow: hidden;
}
.active {
  padding-bottom: 15px;
  color: #282b38;
}
.text {
  font-weight: 400;
  font-size: 17px;
  line-height: 144.5%;
  color: #282b38;
}
.tabs__accordion::before {
  content: "";
  position: absolute;
  display: block;
  right: 0;
  top: -2px;
  background: url(./assets/img/arrow-tabs-right.svg) no-repeat center;
  width: 26px;
  height: 26px;
}
.active-after::after {
  content: "";
  position: absolute;
  display: block;
  right: 0;
  top: -2px;
  background: url(./assets/img/arrow-tabs-top.svg) no-repeat center;
  width: 26px;
  height: 26px;
}

.tabs__accordion.active-before::before {
  display: block;
}

.tabs__accordion.active-before::after {
  display: none;
}

.tabs__accordion.active-after::before {
  display: none;
}

.tabs__accordion.active-after::after {
  display: block;
}

.content {
  display: flex;
}
