#cargo-options {
    .cargo-options {
        &__inner {
            @media (max-width: 991px) {
                padding: 0 0 40px 0;
            }
        }
        // .cargo-options__slider

        &__slider {
            position: relative;
        }
    }

    .slider-item {
        display: flex;
        flex-direction: column;
        max-width: 360px;
        width: 100%;
        background-color: #363a4d;
        border-radius: 20px;
        box-shadow: 5px 15px 40px rgba(30, 35, 66, 0.15);
        margin: 0 15px;

        // .slider-item__image

        &__image {
            min-height: 229px;
            background: center/cover no-repeat;
            border-radius: 20px;
        }

        // .slider-item__content

        &__content {
            display: flex;
            flex-direction: column;
            align-items: center;
            margin: 27px 20px;
        }

        // .slider-item__title

        &__title {
            font-weight: 700;
            font-size: 22px;
            line-height: 124.5%;
            margin-bottom: 13px;
        }

        // .slider-item__period

        &__period {
            margin-bottom: 15px;
            display: flex;
        }

        // .slider-item__period-subtitle

        &__period-subtitle {
            display: flex;
            align-items: center;
            &:not(:last-child) {
                margin-right: 30px;
            }
        }

        // .slider-item__price-weight

        &__price-weight {
            display: flex;
            align-items: baseline;
            margin-bottom: 18px;
            font-weight: 400;
            font-size: 22px;
            line-height: 124.5%;
            color: #a3a3a3;
        }

        // .slider-item__btn-block

        &__btn-block {
            max-width: 320px;
            width: 100%;
            display: flex;
            justify-content: center;
        }
    }
    .period-subtitle {
        // .period-subtitle__calendar

        &__calendar {
            margin-right: 10px;
        }

        // .period-subtitle__text

        &__text {
            font-weight: 400;
            font-size: 16px;
            line-height: 124.5%;
        }

        // .period-subtitle__box

        &__box {
            margin-right: 10px;
        }
    }
    .big-text-price {
        font-size: 36px;
        color: $global-primary-color-text;
        margin-left: 10px;
    }
    .ratio {
        color: $global-primary-color-text;
    }
    .slider-item {
        // .btn-block__button

        &__button {
            padding: 13px 17px;
            border: 1px solid #e58d35;
            &:not(:last-child) {
                margin-right: 14px;
            }
            @media (max-width: 400px) {
                font-size: 10px;
            }
        }
    }
}

.slick-track {
    display: flex;
}
.slick-list {
    overflow: hidden;
}
.slick-dots li {
    position: relative;
    opacity: 0.9;
    font-size: 0;
    margin: 0 13px;
    width: 8px;
    height: 8px;
    border: 2px solid $global-primary-color-text;
    border-radius: 5.625rem;
    list-style: none;
    & button {
        position: absolute;
        width: 8px;
        height: 8px;
        font-size: 0;
        left: -2px;
        top: -2px;
        border-radius: 5.625rem;
        &:hover {
            background-color: $global-primary-color-text;
        }
    }
    &.slick-active {
        background-color: $global-primary-color-text;
    }
}
