#first-screen {
    position: relative;
    .banner {
        position: relative;
    }
    .banner-item {
        background: center/cover no-repeat;
        padding: 170px 0px 60px 0px;
        border-radius: 20px;
    }
    .banner-item {
        // .banner-item__container

        &__container {
            min-height: 470px;
            max-width: 1440px;
            padding: 0 150px;
        }

        // .banner-item__title

        &__title {
            font-weight: 700;
            font-size: 56px;
            max-width: 463px;
            text-transform: uppercase;
            margin-bottom: 8px;
            line-height: 126%;
        }

        // .banner-item__subtitle

        &__subtitle {
            font-weight: 300;
            font-size: 20px;
            line-height: 135.5%;
            max-width: 345px;
            margin-bottom: 37px;
        }
        // .banner-item__button

        &__button {
            display: flex;
            align-items: center;
            position: relative;
            @include btn(600, 1rem, #ffffff, 146%, 5.625rem, #e58d35);
            margin-top: 3rem;
            padding: 23px 94px 23px 33px;
            &:hover {
                background-color: $button-background-hover-color;
            }
            &:active {
                color: $button-text-activ-color;
                transform: scale(1.02);
            }
        }

        // .banner-item__btn-icon

        &__btn-icon {
            position: absolute;
            right: 21px;
        }
    }

    .slick-arrow {
        &.slick-prev {
            left: 150px;
            transform: translateY(-50%) rotate(180deg);
        }
        &.slick-next {
            right: 150px;
            transform: translateY(-50%);
        }
    }
    .slick-list {
        overflow: hidden;
    }
    .slick-track {
        display: flex;
    }
    .slick-dots {
        display: flex;
        justify-content: center;
        position: absolute;
        bottom: 2.4375rem;
        margin: 0;
        padding: 0;
        top: calc(100% - 40px);
    }
    .slick-dots li {
        position: relative;
        opacity: 0.9;
        font-size: 0;
        margin: 0 13px;
        width: 8px;
        height: 8px;
        border: 2px solid $global-primary-color-text;
        border-radius: 5.625rem;
        list-style: none;
        & button {
            position: absolute;
            width: 8px;
            height: 8px;
            font-size: 0;
            left: -2px;
            top: -2px;
            border-radius: 5.625rem;
            &:hover {
                background-color: $global-primary-color-text;
            }
        }
        &.slick-active {
            background-color: $global-primary-color-text;
        }
    }
}
