.about {
    margin-bottom: 2.125rem;
    // .about__header-company
    &__header-company {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__header-company-content {
        max-width: 34.6875rem;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }
    &__header-image {
        max-width: 34.375rem;
    }
    &__img {
        max-width: 100%;
        height: auto;
    }
    // .about__about-subtitle
    &__about-subtitle {
        max-width: 34.6875rem;
    }
    // .about__subtitle
    &__subtitle {
        font-size: 1.125rem;
        line-height: 170%;
        text-align: start;
    }
}
