// container
[class*="__container"] {
    max-width: 73.75rem;
    margin: 0 auto;
    padding: 0 1.25rem;
}

[class*="__container-fluid"] {
    max-width: 120rem;
    margin: 0 auto;
    padding: 0 3.125rem;
}

// section

section {
    margin-bottom: 60px;
}

// header-title + adaptive

[class*="__header-block"] {
    margin-bottom: 70px;
    position: relative;
    text-align: center;
    &::after {
        content: "";
        position: absolute;
        display: block;
        bottom: -20px;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 85px;
        height: 2px;
        background-color: $after-background-color-lightgreen;
    }
}
[class*="after-orange"] {
    &::after {
        background-color: $after-background-color-orange;
    }
}

[class*="__title"] {
    @include decktop-h2title(700, 2.25rem, 124.5%, uppercase);
    margin: 0;
}

@media (max-width: 991px) {
    [class*="__header"] {
        margin-bottom: 50px;
    }

    [class*="__title"] {
        @include mobile-h2title(700, 1.5rem, 124.5%, uppercase);
    }
}

// title-slider - subtitle-slider

[class*="__title-slider"] {
    @include titel-slider(700, 1.375rem, 124.5%, uppercase);
    margin-bottom: 13px;
}

[class*="__subtitle-slider"] {
    @include subtitel-slider(400, 0.9375rem, 124.5%);
}

// buttons

[class*="__button"] {
    @include btn(600, 1rem, #ffffff, 146%, 5.625rem, #e58d35);
    transition: all 0.3s linear;
    border: 1px solid #e58d35;
    display: flex;
    align-items: center;
    &:hover {
        background-color: $button-background-hover-color;
    }
    &:active {
        color: $button-text-activ-color;
        transform: scale(1.02);
    }
}

[class*="btn-bg-transparent"] {
    border: 1px solid #e58d35;
    background-color: transparent;
    transition: all 0.3s linear;
    &:hover {
        background-color: $button-background-color;
    }
    &:active {
        color: $button-text-activ-color;
        transform: scale(1.02);
    }
}

[class*="__btn-icon"] {
    width: 26px;
    height: 26px;
    right: 1em;
}

// forms

label {
    position: relative;
    font-weight: 600;
    font-size: 15px;
    line-height: 126%;
    margin-bottom: 6px;
}
.elements-form {
    margin-bottom: 20px;
}
input {
    font-weight: 400;
    font-size: 15px;
    line-height: 126%;
    border-radius: 10px;
    padding: 18px 20px;
    background-color: $global-primary-color-text;
    border: 1px solid #fff;
    width: 100%;
    &::placeholder {
        color: #cbcbcb;
        transition: 0.2s linear;
    }
    &:focus {
        box-shadow: rgba(44, 44, 63, 1) 0px 0px 10px;
        &::placeholder {
            color: transparent;
        }
    }
    &:focus-visible {
        outline: none;
        border: 1px solid rgb(138, 132, 132);
        box-shadow: rgb(211 211 232 / 82%) 0px 0px 11px 0px;
    }
}

// input type=checkbox

input[type="checkbox"] {
    display: none;
}

input[type="checkbox"] + label::before {
    content: "";
    position: absolute;
    left: 50px;
    width: 24px;
    height: 24px;
    left: 0;
    top: 0;
    border: 1px solid #282b38;
    background-color: #fff;
    border-radius: 5px;
}

input[type="checkbox"] + label::after {
    content: "✓";
    transition: 0.3s;
    opacity: 0;
    position: absolute;
    font-size: 16px;
    color: black;
    left: 8px;
    top: 4px;
}

input[type="checkbox"]:checked + label::after {
    opacity: 1;
}

// input type=number
input[type="number"] {
    -moz-appearance: textfield;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}
