@media screen and (max-width: 575px) {
    // calculation

    .calculation {
        &__button {
            width: 100%;
        }
    }

    // cooperation
    .cooperation {
        &__header {
            margin-bottom: 3.775rem;
        }
        &__info-list {
            margin-bottom: 1rem;
        }
        &__btn {
            padding: 1.4375rem 5.875rem 1.4375rem 2.0625rem;
        }
    }

    #cargo-options {
        .slick-list {
            padding: 0;
            margin: 0;
        }
    }

    .tabs__accordion {
        padding-right: 50px;
    }

    .faq {
        &__form-question {
            max-height: min-content;
        }
    }
}
